<template>
<div>
   <ymt-button @click="handleClick">测试按钮</ymt-button> 

   <img :src='"./images/"+11+".png"' /> 

<img :src='imageUrl()' /> 

   <div class="des">
   		<h3>组件说明</h3>
   		<p>
<pre>
组件名称：YmtButton

样式说明：
1.ymt-btn
2.ymt-btn-default

事件绑定：
@click="自定义事件"

属性说明：
暂无

常见用法：
{{rawHtml}}

</pre>  
   		</p> 
   </div>
</div>


</template>

<script>  
export default { 
	data(){
		return {
			rawHtml:`      <ymt-button @click="handleClick">
        测试按钮
      </ymt-button>`,
      id:11
		}
	},
    methods:{
    	handleClick(){
    		console.log('handleClick-use');
    	},
      imageUrl(){
        return require("./images/11.png");
      }
    }
}
</script>

<style lang="less" scoped> 
	.des {

		margin: 1rem 0 0 0;

		font-size: 1.5rem;

	}
</style>



